.register {
    height:90vh;
    padding:2em;
    padding-top: 10vh;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.registerForm{
    max-width: 500px;
    width:100%;
    display:flex;
    flex-direction: column;
    justify-content: space-evenly;
    background-color: #fff;
    padding: 1em;
    border-radius: 8px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
.registerInput, .registerBtn {
    padding:1em 1.5em;
    width: 100%;
    font-size: 2rem;
    border: none;
    border-radius:5px;
}
.registerInput{
    background-color: #f0f2f5;
    margin-bottom: 1em;
}
.registerInput:hover{
    background-color: #e2e2e2;
}
.registerBtn {
    border-radius:50px;
    background-color: #f0f2f5;
    cursor: pointer;
}
.registerBtn:hover{
    background-color: #b3d1ff;
}
.registerBtn:active{
    background-color: #6ca7ff;
}